#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: 100%;
  height: auto;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
  -webkit-backface-visibility: hidden;
	-webkit-transform: translateZ(0) scale(1.0, 1.0);
	-webkit-filter: grayscale(50%);
	        filter: grayscale(50%);
}
video {
  object-fit: fill;
}

@font-face {
  font-family: customFont;
  src: url(/GUI/oneweather/static/media/Exo-Light.7530c702.otf);
}

html
{
	background-color:#092238;
  color: white;
}

.overAllBackgroundColour
{
	background-color:transparent;
	color: white;
}

*{
	box-sizing: border-box;
	font-family: customFont;
	font-size: 16pt;
}

.LogoContainer
{
    display: inline-block;
    /* border-style: solid;
    border-width: thin; */
    float: right;
    box-sizing: border-box;
    width: 60%;
    padding: 10px;
}

.LogoContainer img
{
	width: 90px;
  	height: 90px;
    display: inline-block;
    float: right;
	/* margin-left: 100px;
    margin-top: 10px; */
}

.LogoContainer h1
{
	font-size: 40pt;
    margin: 0;
    padding-right: 20px;
    display: inline;
    float: right;
    position: relative;
    top: 18px;
    left: 10px;
    /* top: -35px; */
}

#getCurrentLocLinkBtn
{
    width: auto;
    padding: 13px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    border-radius: 25px;
    border:none;
    color:black;
    background-color: white;
}

.rowTopMargin
{
	margin-top: 20px;
}

#currentDate{
  padding-left: 10px;
}

.oneColumn
{
	float: left;
	width: 100%;
	/*padding: 20px;*/

}

.imagepos
{
	position: center;
	margin-left: auto;
	margin-right: auto;
}

.columnTop
{
	float: left;
	width: 50%;
	padding: 10px;
}

.twoColumn
{
	float: left;
  	width: 50%;
}

.columnThreeLayer
{
	float: left;
  	width: 33.33%;
  	margin: 0;

}

.button
{
	background-color:transparent;
	border: 2px white;
	color: white;
	padding: 10px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 25px;
	cursor: pointer;
	width: 100%;
	border-style: solid;
	font-family: Arial;
}

.button:hover
{
	background-color: #1673e5; /* Green */
	color: white;
}

.currentWeatherData
{
	background-color:transparent;
	border: 0;
	color: white;
	padding: 10px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 25px;
