	border-style: solid;
	font-family: Arial;
	width: 100%
}

.row{
		height:auto;
}
.row:after
{
	content: "";
  	display: table;
  	clear: both;
}

#searchBtnDiv{
  padding-left: 20px;
}

#mapBtnDiv{
  padding-right: 20px;
}

.lon
{
  	width: 100%;
  	padding: 10px;
	box-sizing: border-box;
	font-size:13pt;
	font-style: italic;
	margin-left: 10px;
}

.lat
{

  	width: 100%;
  	padding: 10px;
  	box-sizing: border-box;
